import React, { Component, createRef } from "react";
import { Slide } from "react-slideshow-image";
import "react-slideshow-image/dist/styles.css";
import "./Slideshow.css";
import img1 from "../Assets/image1.jpg"
import img2 from "../Assets/VISION-PCs/1-XEROX.png"
import img3 from "../Assets/VISION-PCs/2-HP.png"
import img4 from "../Assets/VISION-PCs/3-EPSON.jpg"
import img5 from "../Assets/VISION-PCs/4-DTF.png"

class Slideshow extends Component {
  constructor() {
    super();
    this.slideRef = createRef();
    this.state = {
      current: 0
    };
  }

  render() {
    const properties = {
      duration: 2000,
      autoplay: true,
      transitionDuration: 1000,
      arrows: true,
      infinite: true,
      easing: "ease",
      swipe: "false"
    };
    const slideImages = [
      {
          url: img1,
          caption: 'Vison Group'
      },
      {
          url: img2,
          caption: 'XEROX'
      },
      {
          url: img3,
          caption: 'HP'
      },
      {
          url: img4,
          caption: 'EPSON'
      },
      {
        url: img5,
        caption: 'DTF'
      }    
  ];
    return (
      <div className="slider-wrapper">
         <div className="slide-container">
        <Slide {...properties}>
         {slideImages.map((slideImage, index)=> (
            <div className="each-slide" key={index}>
              <div style={{'backgroundImage': `url(${slideImage.url})`}} id="image">
                <span className="slide-caption">{slideImage.caption}</span>
              </div>
            </div>
          ))} 
        </Slide>
      </div>
      </div>
    );
  }
}

export default Slideshow;
