import React, { Component } from 'react';
import './AboutPage.css';

function AboutPage() {
    return (
        <div id="about-container">
            <h1>About us</h1>
            <div id="about-info">
                <div id="about1">
                    <p className="about-title">Quality Service</p>
                    <ul>
                        <li><strong>Vision</strong> : <strong>IS</strong> an Egyptian company that built its experience through understanding client requirements. To achieve client satisfaction. Vision team trying to introduce the <em>new printing solution</em> to the client.</li>
                        <li><strong>Vision</strong> : <strong>IS</strong> prefer to establish the suitable solution for each client case by case to make best benefit for the client. </li>
                    </ul>
                </div>
                <div id="about2">
                    <p className="about-title">Services</p>
                    <ul>
                        <li>Machine copier and plotting system <p className="underline">selling</p>.</li>
                        <li>Machine copier and plotting system <p className="underline">supplies</p>.</li>
                        <li>Machine copier and plotting system <p className="underline">maintenance</p>.</li>
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default AboutPage;